.loginPopup{
    width: 100vw;
    height: 100vh;
    background-color: rgba(0,0,0,0.4);
    position:fixed;
    top: 0;
    left: 0;
    display: none;
    opacity: 0;
    pointer-events: none;
    transition: 0.4s;
    align-items: center;
    justify-content: center;
    z-index: 100;
}

.loginPopup.active{
    display: flex;
    opacity: 1;
    pointer-events: all;
}

.loginPopupContent{
    padding: 60px;
    border-radius: 12px;
    background-color: white;
    transform: translate(-3000px, 0);
    transition: transform 0.3s ease;
    position: relative;
}

.loginPopupCloser{
    position: absolute;
    right: 20px;
    top: 20px;
    cursor: pointer;
    opacity: 1;
    transition: opacity 0.3s ease;
}

.loginPopupCloser:hover{
    opacity: 0.6;
}

.loginPopupContent.active{
    transform: translate(0,0); 
}

.loginPopupTitle{
    text-align: center;
    font-size: 19px;
    font-weight: 500;
    text-transform: uppercase;
    margin:0;
    margin-bottom: 20px;
}

.InputHolderLabel{
    font-size: 14px;
    font-weight: 500;
    margin-bottom: 30px;
}

.loginPopupInput{
    width: 380px;
    border: 1px solid #BBB;
    height: 40px;
    padding-left: 20px;
    border-radius: 7px;
    font-size: 14px;
    margin-bottom: 20px;
    transition: all 0.4s ease;
}

.loginPopupInput:hover{
    -webkit-box-shadow: 0px 0px 9px -1px rgba(255, 171, 249, 0.4);
    -moz-box-shadow: 0px 0px 9px -1px rgba(255, 171, 249, 0.4);
    box-shadow: 0px 0px 9px -1px rgba(255, 171, 249, 0.4);
}

.loginPopupPasswordForgotten{
    text-align:right;
    margin-bottom: -2px;
    cursor: pointer;
    color: black;
    transition: color 0.3s ease;
}

.loginPopupPasswordForgotten:hover{
    color: rgb(123, 175, 149);
}

.loginPopupButton{
    width: 100%;
    padding: 12px 0;
    background-color: white;
    color: rgb(57, 124, 91);
    font-size: 16px;
    font-weight: 700;
    cursor:pointer;
    margin-top: 10px;
    border: 2px solid rgb(57, 124, 91);
    opacity: 1;
    transition: opacity 0.3s ease;
    margin-bottom: 30px;
}

.loginPopupButton:hover{
    opacity: 0.5;
}

.loginPopupRegistrationHolder{
    text-align: center;
}

.loginPopupRegistration{
    text-decoration: none;
    color: black;
    cursor: pointer;
    transition: color 0.3s ease;
}

.loginPopupRegistration:hover{
    color:rgb(135, 146, 214)
}