body {
  margin: 0;
}

html {
  box-sizing: border-box;
  font-size: 16px;
  font-family: 'Roboto', sans-serif;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: top;
}

ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

* {
padding: 0px;
margin: 0px;
border: none;
}


a:hover  {
  text-decoration: none;
}

ul, ul li {
list-style: none;
}

img, svg {
max-width: 100%;
height: auto;
}