.logOut {
    margin: 60px;
    text-align: center;
    position: relative;
  }
  
  .logOutTitle {
    font-weight: 500;
    margin-bottom: 30px;
  }
  
  .logOutButton {
    width: 400px;
    height: 40px;
    border: 1px solid black;
    background: none;
    background-color: none;
    transition: background-color 0.3s ease;
    cursor: pointer;
    margin-bottom: 60px;
  }
  
  .logOutButton:hover {
    background: rgb(172, 85, 85);
  }

  .logOutGreeting{
    font-size: 26px;
    font-weight: 700;
  }
  
  .profileFoto{
    margin-bottom: 60px;
    display: flex;
    justify-content:space-between;
    gap: 20px;
  }

  .logOutPhotoAndButtonHolder{
    display: flex;
    gap: 20px;
  }
  
  #profileFotoChanger{
    display: none;
    opacity: 0;
    z-index: -1;
  }

  .logOutGreeting{
    text-align: left;
  }
  
  .profileFotoChangerLabel{
    width: 150px;
    height: 40px;
    padding-top: 8px;
    background-color: #999;
    border-radius: 5px;
    color: #FFF;
    cursor: pointer;
    margin: auto 0;
  }
  
  .profileImg{
    width: 200px;
    height: 200px;
    background-size: cover;
    border-radius: 50%;
  }

  @media (max-width: 750px){
    .profileFoto{
      flex-direction: column;
      align-items: center;
    }

    .logOutGreeting{
      margin-bottom: 50px;
    }

    .logOutPhotoAndButtonHolder{
      margin-bottom: 50px;
    }

    .profileImg{
      width: 120px;
      height: 120px;
    }
  }