.container-slider {
    width: 95vw;
    height: 85vh;
    margin: 0 auto;
    position: relative;
    overflow: hidden;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
    margin-bottom: 100px;
    border-radius: 15px;
    margin-top: 35px;
  }

 @media (max-width: 1000px){
  .container-slider{
    height:70vh;
  }
} 

@media (max-width: 870px){
  .container-slider{
    height:60vh;
  }
}

